<template>
  <div class="CategoryContentItem">
    <a :href="link">
      <img :src="imgSrc" :alt="title" />
      <div>
        <span>{{ title }}</span>
      </div>
    </a>
  </div>
</template>

<script>
export default {
  name: "CategoryContentItem",
  props: {
    imgSrc: {
      type: String,
      default: "",
    },
    title: {
      type: String,
      default: "",
    },
    link: {
      type: String,
      default: "javascript:void(0)",
    },
  },
};
</script>

<style scoped>
.CategoryContentItem img {
  width: 70px;
}
</style>