<template>
  <div class="CategoryList">
    <category-list-item
      v-for="(item, index) in categoryList"
      :key="item.maitKey"
      :isSelect="index === currrentSelect"
      @click.native="goCategoryList(index,item.maitKey)"
      >{{ item.title }}</category-list-item
    >
  </div>
</template>

<script>
import CategoryListItem from "./CategoryListItem.vue";
export default {
  name: "CategoryList",
  data() {
    return {
      currrentSelect: 0,
    };
  },
  props: {
    categoryList: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  methods: {
    goCategoryList(index,maitKey) {
      if (this.currrentSelect !== index) {
        this.currrentSelect = index;
        this.$emit("switch-category",maitKey);
      }
    },
  },
  components: {
    CategoryListItem,
  },
};
</script>

<style scoped>
.CategoryList {
  /* height: calc(100vh - 44px - 49px); */
  height: calc(100vh - 6.59670vh - 7.34632vh);
  overflow-y: scroll;
}
</style>