<template>
  <div class="category">
    <nav-bar class="nav-bar">
      <div slot="center">分类页面</div>
    </nav-bar>
    <div class="categoryMain">
      <category-list
        class="category-list"
        :categoryList="categoryList"
        @switch-category="switchCategoryHandler"
      ></category-list>
      <category-content class="categoryContent" :subcategoryList="subcategoryList"></category-content>
    </div>
  </div>
</template>

<script>
import NavBar from "components/common/navbar/NavBar.vue";
import CategoryList from "./childComps/CategoryList.vue";
import CategoryContent from "./childComps/CategoryContent.vue";

import { getCategoryList, getSubcategory } from "network/category";

export default {
  name: "Category",
  data() {
    return {
      categoryList: [],
      subcategoryList: [],
    };
  },
  created() {
    this.getCategoryList();
  },
  methods: {
    getCategoryList() {
      getCategoryList().then((res) => {
        this.categoryList = res.data.category.list;
        this.getSubcategory(this.categoryList[0].maitKey);
      });
    },
    getSubcategory(maitKey) {
      getSubcategory(maitKey).then((res) => {
        this.subcategoryList = res.data.list;
      });
    },
    switchCategoryHandler(maitKey) {
      this.getSubcategory(maitKey);
    },
  },
  components: {
    NavBar,
    CategoryList,
    CategoryContent,
  },
};
</script>

<style scoped>
.nav-bar {
  background-color: var(--color-tint);
  color: #fff;
  font-weight: bold;
  position: relative;
  z-index: 1;
}
.categoryMain {
  display: flex;
}
.category-list {
  width: 25%;
  flex: 25;
}
.categoryContent{
  width: 75%;
  flex: 75;
}
</style>